//window.jQuery = $;
 //window.$ = $;
//console.log($);
var _=require("lodash");
// require('../../src/chart/line');
// require('../../src/chart/bar');
//require('./src/chart/line');
//require('./src/chart/bar');
//require('imports?$=jquery!../../dist/js/bootstrap.js');
//require('imports?$=jquery!../../dist/js/bootstrap-datetimepicker.min.js');
var today=moment().format('YYYY-MM-DD');
var weeksbfore=moment().subtract(7, 'days').format('YYYY-MM-DD');
var host='http://10.118.10.138:8070/statistic/';
var dates=[],errorDate=[],errorData=[],bookingData=[],replyData=[],cbaData=[];

var optionerror = {
	tooltip: {
		trigger: 'axis'
	},
	legend: {
		data: ['']
	},
	toolbox: {
		show: false,
		feature: {

		}
	},
	xAxis: [{
		type: 'category',
		data:errorDate
	}],
	yAxis: [{
		type: 'value',
		splitArea: {
			show: true
		}
	}],
	series: [{
		name: '',
		type:'line',
		data:errorData
	}]
};
var optionbooking = {
	tooltip: {
		trigger: 'axis'
	},
	legend: {
		data: ['', '','CBA']
	},
	toolbox: {
		show: false,
		feature: {

		}
	},
	xAxis: [{
		type: 'category',
		data:  dates
	}],
	yAxis: [{
		type: 'value',
		splitArea: {
			show: true
		}
	}],
	series: [{
		name: '',
		type:'line',
		data: bookingData
	}, {
		name: '',
		type:'line',
		data: replyData
	},{
		name: 'CBA',
		type:'line',
		data: cbaData
	}]
};

var fetchList=function(){

	var url=host+'historyInfo/getFailureInfoList';
	var result = fetch(url)
	result.then(function(response) {
		return response.json();
	}).then(function(j) {
			console.log(j);
			_.templateSettings.variable = "rc";
			var template = _.template(
				$( "script.template" ).html()
			);
			console.log(template(j.pageResultList));
			$( "table" ).after(
				template(j)
			);

	});


};

var renderChart=function(type,startDate,endDate,chart){
	chart.showLoading({
		textStyle : {
			fontSize : 20
		}
	});
	var bookingurl=host+'getSucessSaveHistory/'+startDate+'/'+endDate;
	var failedurl=host+'getFailureInfoCount/'+startDate+'/'+endDate;
	var url=type===1?bookingurl:failedurl;
	var result = fetch(url)
	result.then(function(response) {
		return response.json();
	}).then(function(j) {
		if(type===1){
			dates=j.flowRecordDate;
			bookingData=j.flowRecordCount[0].recordCount;
			replyData=j.flowRecordCount[1].recordCount;
			cbaData=j.flowRecordCount[2].recordCount;
			optionbooking.xAxis=[{
				type: 'category',
				data:  dates
			}];
			optionbooking.series=[{
				name: '',
				type:'line',
				data: bookingData
			}, {
				name: '',
				type:'line',
				data: replyData
			},{
				name: 'CBA',
				type:'line',
				data: cbaData
			}];
			chart.hideLoading();
			chart.setOption(optionbooking);
		}else{

			errorDate=j.flowRecordDate;
			errorData=j.flowRecordCount;
			optionerror.xAxis = [{
				type: 'category',
				data:  errorDate
			}];
			optionerror.series = [{
				name: '',
				type:'line',
				data:errorData
			}];
			chart.hideLoading();
			chart.setOption(optionerror);
		}
	}).catch(function(ex) {
		console.log('failed', ex)
	});
};

var options={
	locale: 'zh-cn',
	format: 'YYYY-MM-DD',
	defaultDate:today
};

var myChart,myChart2;
module.exports= {
	data:function(){
			return{
					msg:''
			}
	},
	ready:function(){
	  myChart = echarts.init(document.getElementById('booking'));
		myChart2=echarts.init(document.getElementById('error'));
	  fetchList();
		$('#bookingdate').datetimepicker(options);
		$('#errordate').datetimepicker(options);
		$("#bookingdate").on("dp.change", function (e) {
			var changedate=$('#bookingdate').data();
			var todays=changedate.date;
			var weeksbfores=moment(todays).subtract(7, 'days').format('YYYY-MM-DD');
			renderChart(1,weeksbfores,todays,myChart);
		});
		$("#errordate").on("dp.change", function (e) {
			var changedate=$('#errordate').data();
			var todays=changedate.date;
			var weeksbfores=moment(todays).subtract(7, 'days').format('YYYY-MM-DD');
			renderChart(2,weeksbfores,todays,myChart2);
		});
		renderChart(1,weeksbfore,today,myChart);
		renderChart(2,weeksbfore,today,myChart2);
  }
}


